            <h2>Introdução</h2>
            <p>O livro conta os avanços no desenvolvimento pessoal de Hal Elrod, ele quando jovem sofreu um acidente que o deixou em coma por alguns dias e quase o fez perder os movimentos da perna. Após esse evento ele aprende ferramentas que o ajudam a se
                desenvolver como pessoa (adquirir mais conhecimento, disciplina, hábitos saudáveis, produtividade ...). O insight que o faz a começar a investir nisso aconteceu quando ele ouviu a frase <span class="note">“O seu nível de sucesso dificilmente será maior do que o nível de crescimento pessoal ”</span>.Todo
                o restante do livro se baseia nessa afirmação.</p>
            <h2>A Verificação da Realidade de 95% das Pessoas</h2>
            <p>95% da pessoas vivem a vida sem conseguir alcançar os níveis de sucesso que desejam, isso se aplica a qualquer área: profissional, emocional, saúde … . Isso não acontece por falta de desejo ou conhecimento, mas sim por falta de disciplina, motivação
                e estratégia. O livro mostra que a motivação é a força inicial que nos ajuda a começar, mas só terminaremos se tivermos motivação, disciplina e pensamento estratégico. </p>
            <h2>Porque Você Acordou Essa Manhã ?</h2>
            <p>É preciso acordar todo dia de manhã com dedicação e paixão. Alexandre o dono da cacau show diz que ele acorda todo dia para “fazer do caralho” nunca pensa em fazer mais o menos. É preciso ter propósito e buscá-lo não viver na filosofia “deixa
                a vida me levar”.
            </p>
            <h2>A Estratégia de Cinco Passos a Prova da Soneca Para Despertar</h2>
            <ol>
