                <li>Escovar os dentes (movimento gera emoção e emoções também minimizam o sono)</li>
                <li>Tomar um copo de água (depois de horas dormindo seu corpo está desidratado e essa desidratação gera cansaço, beber aguá vai hidratar seu corpo e tirar o cansaço)</li>
                <li>Coloque uma roupa de ginástica e faça alguns exercícios (exercício físico produz endorfina , o hormônio da felicidade)</li>
            </ol>
