                <li>Exercícios</li>
            </ol>
            <p> <span class="note">Motivação te faz começar mas o hábito possibilita finalizar</span> . Um hábito demora 30 dias para ser criado e possui 3 fases: insuportável, desconfortável e imbatível.
            </p>
            <ol>
                <li>Insuportável: Depois que os primeiros dias de motivação passam teremos a sensação de que a dor causada pelo esforço de atingir o objetivo é insuportável, isso é corpo querendo voltar a zona de conforto, - se não desistirmos essa sensação irá
                    enfraquecer com o passar dos tempos.
                </li>
                <li>Desconfortável: Depois que os primeiros 10 dias passam a luta para criar um hábito fica mais fácil, a fase mais difícil já passou, ainda é desconfortável mas já deixou de ser insuportável. </li>
                <li>Imbatível: Nessa fase o objetivo foi alcançado e o nível de satisfação é enorme, teremos o sentimento que podemos fazer qualquer coisa, nos sentiremos imbatíveis.
